/* import tailwind */
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

body {
  width: 100%;
  min-width: 100vw;
  height: 100%;
  min-height: 100vh;
  transition: all 200ms ease-in-out;
}

.menu-link > div {
  visibility: hidden;
}

.menu-link:hover > div {
  visibility: visible;
}
